import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    path: '/home',
    component: Home,
    children: [
      {
        path:'',
        redirect : 'webapps'
      },
      {
        path:'webapps',
        name:'webapps',
        component: ()=>import("../views/home/WebApps.vue")
      },
      {
        path: 'about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/home/About.vue')
      },
    ]
  },
  {
    path:'/happysynth',
    component:()=>import('../views/happysynth/HappySynth.vue'),
    children:[
      {
        path:'',
        redirect: 'play'
      },
      {
        path:'play',
        name:'happysynth-play',
        component:()=>import('../views/happysynth/HappySynthPlay.vue')
      },
      {
        path:'draw',
        name:'happysynth-draw',
        component:()=>import('../views/happysynth/HappySynthDraw.vue')
      }
    ]
  }

]

const router = new VueRouter({
  routes
})

export default router
