<template>
<div class="nav-item" :class="{active:isActive}">
    <slot></slot>
</div>
</template>

<script>
export default {
    name:"NavBarItem",
    computed:{
      isActive:{
        get:function(){
          // console.log(this)
          if(this.$vnode.key===this.$route.name)
            return true
          else return false
        }
      }
    }
}
</script>

<style>
.nav-item {
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  padding: 0 20px;
  white-space: nowrap;
  color: var(--font-color-deactive);
  border-bottom: solid 2px var(--item-bgcolor-deactive);
}
.nav-item img{
  vertical-align: middle;
}
.nav-item.active{
  background-color: var(--item-bgcolor-active);
  border-bottom-color:var(--item-bgcolor-active);
  color: var(--font-color-active);
}
.theme-light .nav-item.active{
  border-bottom-color:var(--item-bordercolor-active);
}
</style>